body, html {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  background-color: #1c1e26;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


.App {
  text-align: center;
  max-width: 1500px;
  margin: 0 auto;
}

.main-content {
  margin-bottom: 50vh;
  background-color: #1c1e26;
}


/*responsive web design tablet*/

@media only screen and (min-width: 700px) {
  .main-content {
      margin-bottom: 30vh;
  }
}

/*responsive web design desktop*/

@media only screen and (min-width: 1000px) {
  .main-content {
      margin-bottom: 50vh;
  }
}

header {
    height: auto;
    width: 100%;
    position: relative;
    color: #fdeb37;
    z-index: 0;
}

.title-wrapper {
    width: 100%;
    padding: 4rem 0 0 0;
}

.header-title {
    font-size: 2.4rem;
    text-transform: uppercase;
}

.job-title {
    letter-spacing: 0.4em;
    font-size: 1.3rem;
}

.tech-wrapper {
    list-style-type: none;
    padding: 0;
    display: block;
    border-left: 2px solid #fdeb37;
}

.tech-wrapper .tech {
    text-align: left;
    font-size: 1rem;
    padding: 0.3rem 1rem;
    -webkit-transition: all 0.4s ease-in-out;
    transition: all 0.4s ease-in-out;
}

.tech-wrapper .tech:hover {
    padding-left: 1.5rem;
}

/*responsive web design tablet*/

@media only screen and (min-width: 700px) {
    header {
        height: 40vh;
        width: 100%;
    }
        
    .title-wrapper {
        position: absolute;
        top: 20%;
        left: 50%;
        -webkit-transform: translateX(-50%);
                transform: translateX(-50%);
    }

    .tech-stack {
        position: absolute;
        top: 70%; 
        left: 50%;
        -webkit-transform: translate(-50%, -50%);
                transform: translate(-50%, -50%);
    }

    .tech-wrapper {
        display: flex;
        border: none;
    }
    
    .tech-wrapper .tech {
        text-align: center;
        border-right: 2px solid #fdeb37;
    }

    .tech-wrapper .tech:last-child {
        border-right: none;
    }
    
    .tech-wrapper .tech:hover {
        padding-left: 1rem;
    }
}

/*responsive web design desktop*/

@media only screen and (min-width: 1000px) {
    header {
        height: 66vh;
    }
    
    .header-title {
        font-size: 4rem;
        letter-spacing: 0.5rem;
    }

    .job-title {
        font-size: 1.6rem;
    }

    .tech-stack {
        top: 70%; 
    }

    .tech-wrapper .tech {
        font-size: 1.3rem;
    }
}

/*responsive web design large desktop*/

@media only screen and (min-width: 1500px) {
    header {
        height: 700px;
    }
}



nav {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    color: white;
    z-index: 1;
    height: 0;
}

.desktop-navbar {
    display: none;
}

/* burger menu styles */

.menu-toggle {
    width:40px;
    height:40px;
    margin: 1rem 0;
    position: absolute;
    top: 10%;
    right: 2%;
    cursor:pointer;
    border-radius:5px;
    z-index: 20;
}

.menu-toggle .hamburger span,
.menu-toggle .cross span {
    display:block;
    background:#fdeb37;
    border-radius:2px;
    -webkit-transition:.25s ease-in-out;
    transition:.25s ease-in-out;
}

.menu-toggle .hamburger {
    width: 100%;
    height: 100%;
}

.menu-toggle .hamburger span {
    width: 80%;
    height: 4px;
    position: relative;
    top: 0px;
    left: 0px;
    margin: 6px auto;
}
.hamburger span:nth-child(1){
    -webkit-transition-delay:.5s;
            transition-delay:.5s;
}
.hamburger span:nth-child(2){
    -webkit-transition-delay:.625s;
            transition-delay:.625s;
}
.hamburger span:nth-child(3){
    -webkit-transition-delay:.75s;
            transition-delay:.75s;
}

.cross {
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}

.cross span:nth-child(1) {
    height:0%;
    width:4px;
    position:absolute;
    top: 0;
    left: 42%;
    -webkit-transition-delay:0s;
            transition-delay:0s;
}
.cross span:nth-child(2) {
    width:0%;
    height:4px;
    position:absolute;
    left:0;
    top: 42%;
    -webkit-transition-delay:.25s;
            transition-delay:.25s;
}

.menu-toggle.open .hamburger span{
    width: 0;
}
.menu-toggle.open .hamburger span:nth-child(1){
    -webkit-transition-delay: 0s;
            transition-delay: 0s;
}
.menu-toggle.open .hamburger span:nth-child(2){
    -webkit-transition-delay: .125s;
            transition-delay: .125s;
}
.menu-toggle.open .hamburger span:nth-child(3){
    -webkit-transition-delay: .25s;
            transition-delay: .25s;
}

.menu-toggle.open .cross span:nth-child(1){
    height: 100%;
    -webkit-transition-delay: .625s;
            transition-delay: .625s;
}
.menu-toggle.open .cross span:nth-child(2){
    width: 100%;
    -webkit-transition-delay: .375s;
            transition-delay: .375s;
}

/*menu overlay styles*/

.overlay-menu {
    height: 100vh;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.8);
    position: relative;
    left: -100%;
    -webkit-transition: all 0.5s ease-in-out;
    transition: all 0.5s ease-in-out;
}

.overlay-menu.open {
    left: 0;
}

.logo-name {
    font-size: 1.5rem;
    text-transform: uppercase;
    letter-spacing: 0.3rem;
    width: 60%;
    position: absolute;
    top: 20px;
    left: 2%; 
    opacity: 0;
    -webkit-transition: opacity 0.3s ease-in-out;
    transition: opacity 0.3s ease-in-out;
}

.logo-name.open {
    opacity: 1;
    -webkit-transition-delay: 0.5s;
            transition-delay: 0.5s;
    z-index: 1;
}

.logo-name span.surname {
    color: #fdeb37;
    border-left: 3px solid #fdeb37;
    padding-left: 1rem;
}

nav .navbar-wrapper {
    text-align: left;
    padding: 4rem 0;
    list-style-type: none;
    margin: auto;
    height: 80px;
}

.navbar-wrapper a {
    text-decoration: none;
    color: white;
}

.navbar-wrapper .nav-item {
    font-size: 1.4rem;
    padding: 1rem 1.5rem;
    letter-spacing: 0.3rem;
    cursor: pointer;
    opacity: 1;
    -webkit-transition: all 0.4s ease-in-out;
    transition: all 0.4s ease-in-out;
}

.navbar-wrapper:hover .nav-item {
    opacity: 0.25
}

.navbar-wrapper .nav-item:hover {
    opacity: 1;
}

/*responsive web design tablet*/

@media only screen and (min-width: 700px) {
    .logo-name {
        width: 30%;
        opacity: 1;
    }
    
    .logo-name.open {
        opacity: 1;
        -webkit-transition-delay: 0.5s;
                transition-delay: 0.5s;
        z-index: 0;
    }
}

/*responsive web design desktop*/

@media only screen and (min-width: 1000px) {
    .menu-toggle {
       display: none;
    }

    .desktop-navbar {
        display: block;
        position: absolute;
        top: 0;
        right: 0;
    }

    .navbar-wrapper-expand {
        display: flex;
        list-style-type: none;
        padding: 0 2rem;
        font-size: 1.5rem;
        -webkit-transition: opacity 0.5s ease-in-out;
        transition: opacity 0.5s ease-in-out;
    }

    .navbar-wrapper-expand a {
        text-decoration: none;
        color: white;
    }

    .nav-item-expand {
        padding: 0 1rem;
        cursor: pointer;
        -webkit-transition: opacity 0.5s ease-in-out;
        transition: opacity 0.5s ease-in-out;
    }

    .navbar-wrapper-expand:hover .nav-item-expand {
        opacity: 0.25;
    }

    .navbar-wrapper-expand .nav-item-expand:hover {
        opacity: 1;
    }
}


#about-wrapper {
    height:auto;
    max-width: 100%;
    margin: 0 auto;
    align-items: center;
    padding: 1.3rem;
}

.profile-wrapper {
    width: 40%;
    height: auto;
    margin: 0 auto;
    overflow: hidden;
    border-radius: 5%;
}

.profile-wrapper img {
    width: 100%;
    height: auto;
}

.about-title {
    color: #fdeb37;
    text-align: left;
}

.description {
    color: #fff;
    text-align: left;
}

/*responsive web design tablet*/

@media only screen and (min-width: 700px) {
    #about-wrapper {
        padding: 0 1.3rem;
    }
    .profile-wrapper {
        overflow: hidden;
        border-radius: 5%;
    }
}

/*responsive web design desktop*/

@media only screen and (min-width: 1000px) {
    #about-wrapper {
        display: flex;
        width: 90%;
    }

    .profile-wrapper {
        flex: 0 1 40%;
    }

    .description-wrapper {
        flex: 1 1 60%;
        padding: 2rem;
        font-size: 1.4rem;
    }
}

#project-wrapper {
    width: 100%;
    height: auto; 
}

.project-title {
    color: #fdeb37;
    text-align: left;
    padding-left: 1.3rem;
}

.project-box figure.project {
    width: 100%;
    max-height: 200px;
    margin: 1rem 0;
    position: relative;
    overflow: hidden;
}

.project img {
    width: 100%;
    height: 100%;
}

figure.project .img-overlay {
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.6);
    opacity: 0;
    -webkit-transition: opacity 1s ease-in-out;
    transition: opacity 1s ease-in-out;
}

figure.project:hover .img-overlay {
    opacity: 1;
}

figure.project figcaption {
    position: absolute;
    font-size: 1.5rem;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    color: #fff;
    opacity: 0;
    -webkit-transition: all 1s ease-in-out;
    transition: all 1s ease-in-out;
}

figure.project figcaption::before {
    content: '';
    width: 100%;
    height: 3px;
    background-color: white;
    position: absolute;
    top: -100%;
    left: 0;
    opacity: 0;
    -webkit-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
}

figure.project figcaption::after {
    content: '';
    width: 100%;
    height: 3px;
    background-color: white;
    position: absolute;
    bottom: -100%;
    left: 0;
    opacity: 0;
    -webkit-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out; 
}

figure.project:hover figcaption {
    opacity: 1;
}

figure.project:hover figcaption::before {
    top: -15%;
    opacity: 1;
}

figure.project:hover figcaption::after {
    bottom: -25%;
    opacity: 1;
}

.project .source-code {
    position: absolute;
    color: white;
    bottom: 20%;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    border: 1px solid #fdeb37;
    padding: 0.5rem;
    opacity: 0;
    text-decoration: none;
    -webkit-transition: all 0.5s ease-in-out;
    transition: all 0.5s ease-in-out;
}

.project:hover .source-code {
    opacity: 1;
}

.source-code:hover {
    background-color: #fdeb37;
    color: black;
}

/*responsive web design tablet*/

@media only screen and (min-width: 700px) {
    .project-title {
        margin: 3rem 0;
    }
    .project-box figure.project {
        max-height: 400px;
    }
}

/*responsive web design desktop*/

@media only screen and (min-width: 1000px) {
    #project-wrapper {
        width: 90%;
        margin: 0 auto;
    }

    .project-title {
        margin: 3rem 0;
        font-size: 2em;
        text-align: center;
    }

    .project-box {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
    }

    .project-box figure.project {
        margin: 0;
    }
}
#contact-wrapper {
